form {
    width: 100%;
}

.form-group {
    display: flex;
    justify-content: center;
	height: 50px;
}

.form-control {
	width: 100%;
	outline: none;
    border: 1px solid darken($color: $grey-100, $amount: 15);
    font-family: 'Lato', sans-serif;
	padding-left: 50px;
    padding-right: 30px;
    border-top-left-radius: 30px;
    border-bottom-left-radius: 30px;
    border-right: 0;
    font-size: 16px;
    color: $grey;
    &:focus {
        box-shadow: 1px 1px 10px 2px $grey-100;
    }
}

.btn--action {
    font-size: 20px;
    padding-left: 20px;
    padding-right: 20px;
    border-top-right-radius: 30px;
    border-bottom-right-radius: 30px;
    border: 1px solid lighten($color: $green, $amount: 10);
    border-left: 0;
    background-color: lighten($color: $green, $amount: 10);
    color: #ffffff;
    outline: none;
    cursor: pointer;
    &:hover {
        background-color: lighten($color: $green, $amount: 5);
    }
}

